import { ParsingContext } from "../../../../chrono";
import { AbstractParserWithWordBoundaryChecking } from "../../../../common/parsers/AbstractParserWithWordBoundary";
import { ParsingComponents, ParsingResult } from "../../../../results";

const NOW_GROUP = 1;
const DAY_GROUP_1 = 2;
const TIME_GROUP_1 = 3;
const TIME_GROUP_2 = 4;
const DAY_GROUP_3 = 5;
const TIME_GROUP_3 = 6;

export default class ZHHansCasualDateParser extends AbstractParserWithWordBoundaryChecking {
    innerPattern(context: ParsingContext): RegExp {
        return new RegExp(
            "(çŽ°åœ¨|ç«‹(?:åˆ»|å³)|å³åˆ»)|" +
                "(ä»Š|æ˜Ž|å‰|å¤§å‰|åŽ|å¤§åŽ|æ˜¨)(æ—©|æ™š)|" +
                "(ä¸Š(?:åˆ)|æ—©(?:ä¸Š)|ä¸‹(?:åˆ)|æ™š(?:ä¸Š)|å¤œ(?:æ™š)?|ä¸­(?:åˆ)|å‡Œ(?:æ™¨))|" +
                "(ä»Š|æ˜Ž|å‰|å¤§å‰|åŽ|å¤§åŽ|æ˜¨)(?:æ—¥|å¤©)" +
                "(?:[\\s|,|ï¼Œ]*)" +
                "(?:(ä¸Š(?:åˆ)|æ—©(?:ä¸Š)|ä¸‹(?:åˆ)|æ™š(?:ä¸Š)|å¤œ(?:æ™š)?|ä¸­(?:åˆ)|å‡Œ(?:æ™¨)))?",
            "i"
        );
    }

    innerExtract(context: ParsingContext, match: RegExpMatchArray): ParsingComponents | ParsingResult {
        const index = match.index;
        const result = context.createParsingResult(index, match[0]);

        const refDate = context.refDate;
        let date = new Date(refDate.getTime());

        if (match[NOW_GROUP]) {
            result.start.imply("hour", refDate.getHours());
            result.start.imply("minute", refDate.getMinutes());
            result.start.imply("second", refDate.getSeconds());
            result.start.imply("millisecond", refDate.getMilliseconds());
        } else if (match[DAY_GROUP_1]) {
            const day1 = match[DAY_GROUP_1];
            const time1 = match[TIME_GROUP_1];

            if (day1 == "æ˜Ž") {
                // Check not "Tomorrow" on late night
                if (refDate.getHours() > 1) {
                    date.setDate(date.getDate() + 1);
                }
            } else if (day1 == "æ˜¨") {
                date.setDate(date.getDate() - 1);
            } else if (day1 == "å‰") {
                date.setDate(date.getDate() - 2);
            } else if (day1 == "å¤§å‰") {
                date.setDate(date.getDate() - 3);
            } else if (day1 == "åŽ") {
                date.setDate(date.getDate() + 2);
            } else if (day1 == "å¤§åŽ") {
                date.setDate(date.getDate() + 3);
            }

            if (time1 == "æ—©") {
                result.start.imply("hour", 6);
            } else if (time1 == "æ™š") {
                result.start.imply("hour", 22);
                result.start.imply("meridiem", 1);
            }
        } else if (match[TIME_GROUP_2]) {
            const timeString2 = match[TIME_GROUP_2];
            const time2 = timeString2[0];
            if (time2 == "æ—©" || time2 == "ä¸Š") {
                result.start.imply("hour", 6);
            } else if (time2 == "ä¸‹") {
                result.start.imply("hour", 15);
                result.start.imply("meridiem", 1);
            } else if (time2 == "ä¸­") {
                result.start.imply("hour", 12);
                result.start.imply("meridiem", 1);
            } else if (time2 == "å¤œ" || time2 == "æ™š") {
                result.start.imply("hour", 22);
                result.start.imply("meridiem", 1);
            } else if (time2 == "å‡Œ") {
                result.start.imply("hour", 0);
            }
        } else if (match[DAY_GROUP_3]) {
            const day3 = match[DAY_GROUP_3];

            if (day3 == "æ˜Ž") {
                // Check not "Tomorrow" on late night
                if (refDate.getHours() > 1) {
                    date.setDate(date.getDate() + 1);
                }
            } else if (day3 == "æ˜¨") {
                date.setDate(date.getDate() - 1);
            } else if (day3 == "å‰") {
                date.setDate(date.getDate() - 2);
            } else if (day3 == "å¤§å‰") {
                date.setDate(date.getDate() - 3);
            } else if (day3 == "åŽ") {
                date.setDate(date.getDate() + 2);
            } else if (day3 == "å¤§åŽ") {
                date.setDate(date.getDate() + 3);
            }

            const timeString3 = match[TIME_GROUP_3];
            if (timeString3) {
                const time3 = timeString3[0];
                if (time3 == "æ—©" || time3 == "ä¸Š") {
                    result.start.imply("hour", 6);
                } else if (time3 == "ä¸‹") {
                    result.start.imply("hour", 15);
                    result.start.imply("meridiem", 1);
                } else if (time3 == "ä¸­") {
                    result.start.imply("hour", 12);
                    result.start.imply("meridiem", 1);
                } else if (time3 == "å¤œ" || time3 == "æ™š") {
                    result.start.imply("hour", 22);
                    result.start.imply("meridiem", 1);
                } else if (time3 == "å‡Œ") {
                    result.start.imply("hour", 0);
                }
            }
        }

        result.start.assign("day", date.getDate());
        result.start.assign("month", date.getMonth() + 1);
        result.start.assign("year", date.getFullYear());

        return result;
    }
}
