const PATTERN = `(?:(?:Ð¿Ñ€Ð¸Ð±Ð»Ð¸Ð·Ð½Ð¾|Ð¾Ñ€Ñ–Ñ”Ð½Ñ‚Ð¾Ð²Ð½Ð¾)\\s*(?:~\\s*)?)?(${TIME_UNITS_PATTERN})${REGEX_PARTS.rightBoundary}`;

