import { Parser, ParsingContext } from "../../../chrono";
import { toHankaku } from "../constants";
import { findYearClosestToRef } from "../../../calculation/years";

const PATTERN =
    /(?:(?:([åŒä»Šæœ¬])|((æ˜­å’Œ|å¹³æˆ|ä»¤å’Œ)?([0-9ï¼-ï¼™]{1,4}|å…ƒ)))å¹´\s*)?([0-9ï¼-ï¼™]{1,2})æœˆ\s*([0-9ï¼-ï¼™]{1,2})æ—¥/i;
const SPECIAL_YEAR_GROUP = 1;
const TYPICAL_YEAR_GROUP = 2;
const ERA_GROUP = 3;
const YEAR_NUMBER_GROUP = 4;
const MONTH_GROUP = 5;
const DAY_GROUP = 6;

export default class JPStandardParser implements Parser {
    pattern() {
        return PATTERN;
    }

    extract(context: ParsingContext, match: RegExpMatchArray) {
        const month = parseInt(toHankaku(match[MONTH_GROUP]));
        const day = parseInt(toHankaku(match[DAY_GROUP]));
        const components = context.createParsingComponents({
            day: day,
            month: month,
        });

        if (match[SPECIAL_YEAR_GROUP] && match[SPECIAL_YEAR_GROUP].match("åŒ|ä»Š|æœ¬")) {
            components.assign("year", context.reference.getDateWithAdjustedTimezone().getFullYear());
        }

        if (match[TYPICAL_YEAR_GROUP]) {
            const yearNumText = match[YEAR_NUMBER_GROUP];

            let year = yearNumText == "å…ƒ" ? 1 : parseInt(toHankaku(yearNumText));
            if (match[ERA_GROUP] == "ä»¤å’Œ") {
                year += 2018;
            } else if (match[ERA_GROUP] == "å¹³æˆ") {
                year += 1988;
            } else if (match[ERA_GROUP] == "æ˜­å’Œ") {
                year += 1925;
            }

            components.assign("year", year);
        } else {
            const year = findYearClosestToRef(context.refDate, day, month);
            components.imply("year", year);
        }

        return components;
    }
}
