import { ParsingContext } from "../../../chrono";
import { ParsingComponents } from "../../../results";
import { Meridiem } from "../../../types";
import { AbstractTimeExpressionParser } from "../../../common/parsers/AbstractTimeExpressionParser";
import { REGEX_PARTS } from "../constants";

export default class UKTimeExpressionParser extends AbstractTimeExpressionParser {
    constructor(strictMode) {
        super(strictMode);
    }

    patternFlags(): string {
        return REGEX_PARTS.flags;
    }

    primaryPatternLeftBoundary(): string {
        return `(^|\\s|T|(?:[^\\p{L}\\p{N}_]))`;
    }

    followingPhase(): string {
        return `\\s*(?:\\-|\\â€“|\\~|\\ã€œ|Ð´Ð¾|Ñ–|Ð¿Ð¾|\\?)\\s*`;
    }

    primaryPrefix(): string {
        return `(?:(?:Ð²|Ñƒ|Ð¾|Ð¾Ð±|Ð·|Ñ–Ð·|Ð²Ñ–Ð´)\\s*)??`;
    }

    primarySuffix(): string {
        return `(?:\\s*(?:Ñ€Ð°Ð½ÐºÑƒ|Ð²ÐµÑ‡Ð¾Ñ€Ð°|Ð¿Ð¾ Ð¾Ð±Ñ–Ð´Ñ–|Ð¿Ñ–ÑÐ»Ñ Ð¾Ð±Ñ–Ð´Ñƒ))?(?!\\/)${REGEX_PARTS.rightBoundary}`;
    }

    extractPrimaryTimeComponents(context: ParsingContext, match: RegExpMatchArray): null | ParsingComponents {
        const components = super.extractPrimaryTimeComponents(context, match);
        if (components) {
            if (match[0].endsWith("Ð²ÐµÑ‡Ð¾Ñ€Ð°")) {
                const hour = components.get("hour");
                if (hour >= 6 && hour < 12) {
                    components.assign("hour", components.get("hour") + 12);
                    components.assign("meridiem", Meridiem.PM);
                } else if (hour < 6) {
                    components.assign("meridiem", Meridiem.AM);
                }
            }

            if (match[0].endsWith("Ð¿Ð¾ Ð¾Ð±Ñ–Ð´Ñ–") || match[0].endsWith("Ð¿Ñ–ÑÐ»Ñ Ð¾Ð±Ñ–Ð´Ñƒ")) {
                components.assign("meridiem", Meridiem.PM);
                const hour = components.get("hour");
                if (hour >= 0 && hour <= 6) {
                    components.assign("hour", components.get("hour") + 12);
                }
            }

            if (match[0].endsWith("Ñ€Ð°Ð½ÐºÑƒ")) {
                components.assign("meridiem", Meridiem.AM);
                const hour = components.get("hour");
                if (hour < 12) {
                    components.assign("hour", components.get("hour"));
                }
            }
        }

        return components;
    }
}
