import { Parser, ParsingContext } from "../../../chrono";
import { Meridiem } from "../../../types";
import * as references from "../../../common/casualReferences";

const PATTERN = /ä»Šæ—¥|ãã‚‡ã†|æœ¬æ—¥|ã»ã‚“ã˜ã¤|æ˜¨æ—¥|ãã®ã†|æ˜Žæ—¥|ã‚ã—ãŸ|ä»Šå¤œ|ã“ã‚“ã‚„|ä»Šå¤•|ã“ã‚“ã‚†ã†|ä»Šæ™©|ã“ã‚“ã°ã‚“|ä»Šæœ|ã‘ã•/i;

function normalizeTextToKanji(text: string) {
    switch (text) {
        case "ãã‚‡ã†":
            return "ä»Šæ—¥";
        case "ã»ã‚“ã˜ã¤":
            return "æœ¬æ—¥";
        case "ãã®ã†":
            return "æ˜¨æ—¥";
        case "ã‚ã—ãŸ":
            return "æ˜Žæ—¥";
        case "ã“ã‚“ã‚„":
            return "ä»Šå¤œ";
        case "ã“ã‚“ã‚†ã†":
            return "ä»Šå¤•";
        case "ã“ã‚“ã°ã‚“":
            return "ä»Šæ™©";
        case "ã‘ã•":
            return "ä»Šæœ";
        default:
            return text;
    }
}

export default class JPCasualDateParser implements Parser {
    pattern() {
        return PATTERN;
    }

    extract(context: ParsingContext, match: RegExpMatchArray) {
        const text = normalizeTextToKanji(match[0]);

        const components = context.createParsingComponents();

        switch (text) {
            case "æ˜¨æ—¥":
                return references.yesterday(context.reference);

            case "æ˜Žæ—¥":
                return references.tomorrow(context.reference);

            case "æœ¬æ—¥":
            case "ä»Šæ—¥":
                return references.today(context.reference);
        }

        if (text == "ä»Šå¤œ" || text == "ä»Šå¤•" || text == "ä»Šæ™©") {
            components.imply("hour", 22);
            components.assign("meridiem", Meridiem.PM);
        } else if (text.match("ä»Šæœ")) {
            components.imply("hour", 6);
            components.assign("meridiem", Meridiem.AM);
        }

        const date = context.refDate;
        components.assign("day", date.getDate());
        components.assign("month", date.getMonth() + 1);
        components.assign("year", date.getFullYear());
        return components;
    }
}
