import { ParsingContext } from "../../../chrono";
import { ParsingComponents, ParsingResult } from "../../../results";
import * as references from "../../../common/casualReferences";
import { AbstractParserWithLeftRightBoundaryChecking } from "./AbstractParserWithWordBoundaryChecking";

export default class UKCasualDateParser extends AbstractParserWithLeftRightBoundaryChecking {
    innerPatternString(context: ParsingContext): string {
        return `(?:Ð·|Ñ–Ð·|Ð²Ñ–Ð´)?\\s*(ÑÑŒÐ¾Ð³Ð¾Ð´Ð½Ñ–|Ð²Ñ‡Ð¾Ñ€Ð°|Ð·Ð°Ð²Ñ‚Ñ€Ð°|Ð¿Ñ–ÑÐ»ÑÐ·Ð°Ð²Ñ‚Ñ€Ð°|Ð¿Ñ–ÑÐ»ÑÐ¿Ñ–ÑÐ»ÑÐ·Ð°Ð²Ñ‚Ñ€Ð°|Ð¿Ð¾Ð·Ð°Ð¿Ð¾Ð·Ð°Ð²Ñ‡Ð¾Ñ€Ð°|Ð¿Ð¾Ð·Ð°Ð²Ñ‡Ð¾Ñ€Ð°)`;
    }

    innerExtract(context: ParsingContext, match: RegExpMatchArray): ParsingComponents | ParsingResult {
        const lowerText = match[1].toLowerCase();
        const component = context.createParsingComponents();

        switch (lowerText) {
            case "ÑÑŒÐ¾Ð³Ð¾Ð´Ð½Ñ–":
                return references.today(context.reference);

            case "Ð²Ñ‡Ð¾Ñ€Ð°":
                return references.yesterday(context.reference);

            case "Ð·Ð°Ð²Ñ‚Ñ€Ð°":
                return references.tomorrow(context.reference);

            case "Ð¿Ñ–ÑÐ»ÑÐ·Ð°Ð²Ñ‚Ñ€Ð°":
                return references.theDayAfter(context.reference, 2);

            case "Ð¿Ñ–ÑÐ»ÑÐ¿Ñ–ÑÐ»ÑÐ·Ð°Ð²Ñ‚Ñ€Ð°":
                return references.theDayAfter(context.reference, 3);

            case "Ð¿Ð¾Ð·Ð°Ð²Ñ‡Ð¾Ñ€Ð°":
                return references.theDayBefore(context.reference, 2);

            case "Ð¿Ð¾Ð·Ð°Ð¿Ð¾Ð·Ð°Ð²Ñ‡Ð¾Ñ€Ð°":
                return references.theDayBefore(context.reference, 3);
        }

        return component;
    }
}
