export const NUMBER_PATTERN = `(?:${matchAnyPattern(
    INTEGER_WORD_DICTIONARY
)}|[0-9]+|[0-9]+\\.[0-9]+|Ð¿Ñ–Ð²|Ð´ÐµÐºÑ–Ð»ÑŒÐºÐ°|Ð¿Ð°Ñ€(?:Ñƒ)|\\s{0,3})`;

export function parseNumberPattern(match: string): number {
    const num = match.toLowerCase();
    if (INTEGER_WORD_DICTIONARY[num] !== undefined) {
        return INTEGER_WORD_DICTIONARY[num];
    }
    if (num.match(/Ð´ÐµÐºÑ–Ð»ÑŒÐºÐ°/)) {
        return 2;
    } else if (num.match(/Ð¿Ñ–Ð²/)) {
        return 0.5;
    } else if (num.match(/Ð¿Ð°Ñ€/)) {
        return 2;
    } else if (num === "") {
        return 1;
    }
    return parseFloat(num);
}

export const ORDINAL_NUMBER_PATTERN = `(?:${matchAnyPattern(ORDINAL_WORD_DICTIONARY)}|[0-9]{1,2}(?:Ð³Ð¾|Ð¾Ð³Ð¾|Ðµ)?)`;
export function parseOrdinalNumberPattern(match: string): number {
    const num = match.toLowerCase();
    if (ORDINAL_WORD_DICTIONARY[num] !== undefined) {
        return ORDINAL_WORD_DICTIONARY[num];
    }
    return parseInt(num);
}

const year = "(?:\\s+(?:Ñ€Ð¾ÐºÑƒ|Ñ€Ñ–Ðº|Ñ€|Ñ€.))?";
export const YEAR_PATTERN = `(?:[1-9][0-9]{0,3}${year}\\s*(?:Ð½.Ðµ.|Ð´Ð¾ Ð½.Ðµ.|Ð½. Ðµ.|Ð´Ð¾ Ð½. Ðµ.)|[1-2][0-9]{3}${year}|[5-9][0-9]${year})`;
export function parseYearPattern(match: string): number {
    if (/(Ñ€Ñ–Ðº|Ñ€Ð¾ÐºÑƒ|Ñ€|Ñ€.)/i.test(match)) {
        match = match.replace(/(Ñ€Ñ–Ðº|Ñ€Ð¾ÐºÑƒ|Ñ€|Ñ€.)/i, "");
    }

    if (/(Ð´Ð¾ Ð½.Ðµ.|Ð´Ð¾ Ð½. Ðµ.)/i.test(match)) {
        //Before Common Era
        match = match.replace(/(Ð´Ð¾ Ð½.Ðµ.|Ð´Ð¾ Ð½. Ðµ.)/i, "");
        return -parseInt(match);
    }

    if (/(Ð½. Ðµ.|Ð½.Ðµ.)/i.test(match)) {
        //Common Era
        match = match.replace(/(Ð½. Ðµ.|Ð½.Ðµ.)/i, "");
        return parseInt(match);
    }

    const rawYearNumber = parseInt(match);
    return findMostLikelyADYear(rawYearNumber);
}

const SINGLE_TIME_UNIT_PATTERN = `(${NUMBER_PATTERN})\\s{0,3}(${matchAnyPattern(TIME_UNIT_DICTIONARY)})`;
const SINGLE_TIME_UNIT_REGEX = new RegExp(SINGLE_TIME_UNIT_PATTERN, "i");

export const TIME_UNITS_PATTERN = repeatedTimeunitPattern(
    `(?:(?:Ð±Ð»Ð¸Ð·ÑŒÐºÐ¾|Ð¿Ñ€Ð¸Ð±Ð»Ð¸Ð·Ð½Ð¾)\\s{0,3})?`,
    SINGLE_TIME_UNIT_PATTERN
);

export function parseDuration(timeunitText): Duration {
    const fragments = {};
    let remainingText = timeunitText;
    let match = SINGLE_TIME_UNIT_REGEX.exec(remainingText);
    while (match) {
        collectDateTimeFragment(fragments, match);
        remainingText = remainingText.substring(match[0].length).trim();
        match = SINGLE_TIME_UNIT_REGEX.exec(remainingText);
    }
    return fragments as Duration;
}

function collectDateTimeFragment(fragments, match) {
    const num = parseNumberPattern(match[1]);
    const unit = TIME_UNIT_DICTIONARY[match[2].toLowerCase()];
    fragments[unit] = num;
}
